.header {
	width: 100%;
	height: 55px;
	direction: rtl;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: rgb(25, 0, 105);
	&__buttons {
		color: white;
		display: flex;
		margin: 0 1.3em;
	}
	&__burger {
		display: none;
	}
}

.logo {
	margin: 0;
	padding: 0;
	font-size: 1.6em !important;
}
